@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

.App {
  height: 100svh;
  background-color: rgb(32, 32, 32);
}

.main {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.appbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px 16px;
}

.appbar-title {
  font-weight: bold;
  font-size: 18px;
  color: rgb(256, 250, 250);
}

.section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 24px;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  color: rgb(250, 250, 250);
  margin-bottom: 20px;
}

.slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  border: 2px solid rgb(64, 255, 128);
  border-radius: 15px;
  padding: 8px 16px;
}

.arrow {
  background: none;
  border: none;
  font-size: 24px;
  font-weight: bolder;
  color: rgb(250, 250, 250);
  cursor: pointer;
}

.slider-text {
  font-size: 20px;
  font-weight: 500;
  color: rgb(250, 250, 250);
}

.selection-part {
  display: flex;
  flex-direction: row;
  margin: 12px 0;
}

.selections {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
  color: rgb(250, 250, 250);
  border-radius: 15px;
  padding: 8px 16px;
}

.start-test {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
  color: rgb(250, 250, 250);
  border: 2px solid rgb(250, 250, 250);
  border-radius: 15px;
  margin-bottom: 8px;
  padding: 8px 16px;
  font-size: 18px;
}

.onTest {
  height: 100svh;
  display: flex;
  flex-direction: column;
}

.onTest .index {
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  font-family: 'TheJamsil5Bold';
  color: rgb(256, 250, 250);
  padding: 8px 0;
}

.test {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 16px 16px;
}

.question {
  flex: 3.5;
  display: flex;
  flex-direction: column;
  border: solid 2px rgb(250, 250, 250);
  border-radius: 16px;
  color: rgb(250, 250, 250);
  font-size: 16px;
  margin: 0 2px 20px 2px;
}

.correct_answer {
  flex: 3.5;
  display: flex;
  flex-direction: column;
  border: solid 2px rgb(250, 250, 250);
  border-radius: 16px;
  color: rgb(250, 250, 250);
  font-size: 16px;
  margin: 0 2px 20px 2px;
}

.outline-box {
  position: relative;
}

.text-box {
  position: absolute;
  left: calc(50% - 20px);
  bottom: -9px;
  font-size: 16px;
  background-color: rgb(32, 32, 32);
  padding: 0 8px;
}

.content-box {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: rgb(250, 250, 250);
  padding: 16px 16px;
}

.test .bt{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border: solid 2px rgb(250, 250, 250);
  border-radius: 16px;
  color: rgb(250, 250, 250);
  font-size: 20px;
  margin: 0 2px;
}
